<template>
  <div class="wrapper">
    <ItemList @selectedItem="getSelectedItem" />
    <DeskTopCard
      v-if="isclicked"
      :selectedItem="this.selectedItem"
      @isclicked="closeModal"
    />
  </div>
</template>

<script>
import ItemList from "../ItemList.vue";
import DeskTopCard from "./DeskTopCard.vue";
export default {
  data: () => ({
    selectedItem: [],
    isclicked: false,
  }),
  components: {
    ItemList,
    DeskTopCard,
  },
  methods: {
    getSelectedItem(item) {
      document.body.style.position = "fixed";
      this.selectedItem = item;
      this.isclicked = true;
    },
    closeModal() {
      document.body.style.position = "";
      this.isclicked = false;
      console.log("close");
    },
  },
};
</script>

<style src="./DeskTopVue.css" scoped></style>
